.footer {
  margin: 5px 10px;
  border-radius: 3px;
  font-weight: 300;
  background-color: white;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  list-style-type: none
}
ul {
  list-style-position: inside;
  padding: 0;
  margin:0;
}
ul.app_copyright{
  padding: 0;
  margin: 0;
  color:#3A4145;
  font-weight: bold;
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 11px; 
}
li{
      padding: 10px 10px;
}
.footer__links {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  list-style-type: none;
  width: 100%;
}
.footer__links a,
.footer__links a:hover,
.footer__links a:visited{
  color:#3A4145;
  text-decoration: none;
}

.footer__links a:hover{
  text-decoration: underline;
}

.footer__links_twitter{
}
@media screen and (max-width : 400px) {
  .footer__links {
    width: 95%;
  }
}



