.career {
  /*background #222 url('/assets/transparent.jpg') center / cover*/
  font-size: 16px;
  font-weight: 300;
    border-bottom: 2px solid #f4f5f6;
  padding: 5px 0px;
  margin: 10px 10px;
}

/* COMPANY DESCRIPTION */

.career__company, .career__company__description {
    padding: 5px 0px;
    margin: 10px 5px;
}
.career__company{
  font-size: 1.5em;
   font-weight: bold;
}
.career__company__name{
  font-weight: 300;
  font-size: 1.25em;
  padding-bottom: 15px;
}
.career__company__description {
}


/* JOB DESCRIPTION */

.career__job, .career__job__description{
  padding: 5px 30px;
  margin: 10px 10px;
}
.career__job{
   font-weight: 300;
  font-size: 1.3em;  
}
.career__job__description{
}
.career__performance, .career__performance__list{
  padding: 5px 30px;
  margin: 10px 10px;
}
.career__performance{
  font-size: 1.5em;
   font-weight: 300;
}
.career__performance__list{
  list-style-type: circle;
  line-height: 1.75em;
}
.career li{
  margin-bottom: 1em;
}
.career li:hover {
  background-color: white;
  /*padding: 10px 10px;*/
  border-radius: 3px;
  /*box-shadow: 1px 1px 1px;*/
}

@media screen and (max-width : 400px) {
  .career {
    width: 90%;
    display : -ms-flexbox;
    display : flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .career__company, .career__job,
  .career__performance {
    -ms-flex-item-align: end;
        align-self: flex-end;
    font-size: 1em;
  }
  .career__job,
  .career__job__description,
  .career__performance,
  .career__performance__list {
    padding: 5px 0px;
  }
  p, li{
    font-size: 14px;
  }

  
}




